<template>
    <div class="picture" style="line-height:25px">
        <el-row>
            <el-col :span="21">
                <!-- 模糊检索 -->
                <div class="grid-content bg-purple">
                    <el-form :inline="true" :model="pictureFormLike" class="demo-form-inline">
                        <el-form-item label="图片名称">
                            <el-input v-model="pictureFormLike.pictureName" placeholder="请输入图片名称" clearable></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" @click="search">查询</el-button>
                        </el-form-item>
                    </el-form>
                </div>
            </el-col>
            <el-col :span="3">
                <div class="grid-content bg-purple-light">
                    <el-button type="primary" @click="insert">新增</el-button>
                </div>
            </el-col>
        </el-row>
        
        <!--功能列表-->
        <el-row>
            <el-table :data="tableData" class="tableBox" style="width: 100%">
                <el-table-column prop="pictureName" label="图片名称" ></el-table-column>
                <el-table-column prop="pictureUrl" label="图片地址" :show-overflow-tooltip='true' >
                    <template slot-scope="props">
                        <el-link type="primary" @click="clickPictureUrl(props.row)" :underline="false">{{props.row.pictureUrl}}</el-link>
                    </template>
                </el-table-column>
                <el-table-column prop="createPeople" label="创建人" ></el-table-column>
                <el-table-column prop="createTime" label="创建时间" ></el-table-column>
                <el-table-column prop="uodatePeople" label="修改人" ></el-table-column>
                <el-table-column prop="uodateTime" label="修改时间" ></el-table-column>  

                <el-table-column width="300" label="操作">
                    <template slot-scope="scope">
                        <el-button @click="toEditPro(scope.row)" size="small">编辑</el-button>
                        <el-button @click="toDelPicture(scope.row)" type="danger" size="small">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </el-row>

        <!--分页功能-->
        <el-row>
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="[5, 10, 20, 100, 200, 300, 400]"
                :page-size="pageSize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total"
            >
            </el-pagination>
        </el-row>

        <!-- 新增或修改模态框 -->
        <el-row>
            <el-dialog title="图片管理" :visible.sync="editProState">
                <el-form :model="pictureForm">
                    <!-- <el-form-item label="图片名称">
                        <el-input v-model="pictureForm.pictureName" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="图片地址">
                        <el-input v-model="pictureForm.pictureUrl" auto-complete="off"></el-input>
                    </el-form-item> -->
                    <el-upload ref="upload" action='' :on-change="getFile" :limit="1" list-type="picture" :auto-upload="false">
                        <el-button size="small" type="primary">选择图片上传,最多上传一张图片</el-button>
                    </el-upload>
                </el-form>
                <!-- 按钮 -->
                <div slot="footer" class="dialog-footer">
                    <el-button @click="editProState = false">取 消</el-button>
                    <el-button type="primary" @click="saveInfo">确 定</el-button>
                </div>
            </el-dialog>
        </el-row>
    </div>
</template>

<script>

  export default {
    data() {
      return {
        pictureFormLike:{
            pictureName: ''
        },
        tableData: [],
        currentPage: 1,
        pageSize: 5,
        total: 100,
        pictureForm: {
            pictureName: '',
            pictureUrl: ''
        },
        editProState: false
      }
    },
    created: function(){
        this.load();  //页面初始化时，加载的方法
    },
    methods: {
        //初始化数据
        load() {
            var _this = this;
            this.$http({
                method: 'post',
                url: '/Picture/getPage',
                data: {pageCode:this.currentPage,pageSize:this.pageSize,pictureName:this.pictureFormLike.pictureName}
            }).then(res => {
                if (res.status == 200) {
                    _this.tableData = res.data.list;
                    _this.total = res.data.total;
                }
                
            });
        },
        //模糊检索
        search() {
            this.load();
        },
        //每页显示数量发生变化
        handleSizeChange(val) {
            this.pageSize = val;
            this.load();
        },
        //第几页发生变化
        handleCurrentChange(val) {
            this.currentPage = val;
            this.load();
        },
        //点击新增按钮
        insert(){
            this.pictureForm = {};
            this.editProState = true;
            this.$refs.upload.clearFiles();
        },
        //点击编辑按钮
        toEditPro(val) {
            this.pictureForm = {}
            let id = val.id;
            this.$http({
                method: 'get',
                url: '/Picture/getById',
                params: {
                    id:id
                }
            }).then(res => {
                if(res.data.code == 200){
                    this.pictureForm = res.data.data;
                    this.editProState = true;
                    this.$refs.upload.clearFiles();
                }
            });
        },
        //新增或修改表单保存
        saveInfo(){
            console.log("保存的数据为：",this.pictureForm)
            if(this.pictureForm.id != null){
                this.pictureForm.uodatePeople = sessionStorage.getItem("name");
                //修改
                this.$http({
                    method: 'post',
                    url: '/Picture/updateById',
                    data: this.pictureForm
                }).then(res => {
                    let code = res.data.code;
                    if(code == 200){
                        this.editProState = false;
                        this.load();
                        this.$message.success({
                            message: res.data.msg
                        })
                    }else{
                        this.$message.error({
                            message: res.data.msg
                        })
                    }
                });
            }else{
                this.pictureForm.createPeople = sessionStorage.getItem("name");
                //新增
                this.$http({
                    method: 'post',
                    url: '/Picture/add',
                    data:this.pictureForm
                }).then(res => {
                    let code = res.data.code;
                    if(code == 200){
                        this.editProState = false;
                        this.load();
                        this.$message.success({
                            message: res.data.msg
                        })
                    }else{
                        this.$message.error({
                            message: res.data.msg
                        })
                    }
                });
            }
        },
        //删除
        toDelPicture(val){
            this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning",
            }).then(() => {
                let id = val.id;
                this.$http({
                    method: 'get',
                    url: '/Picture/delete',
                    params: {
                        id:id
                    }
                }).then(res => {
                    if(res.data.code == 200){
                        this.load();
                        this.$message.success({
                            message: res.data.msg
                        })
                    }else{
                        this.$message.error({
                            message: res.data.msg
                        })
                    }
                });
            });
        },
        getFile(file, fileList) {
            var formData = {
                pictureName: file.name,
                pictureUrl: ''
            }
            this.getBase64(file.raw).then(res => {
                formData.pictureUrl = res
                this.$nextTick(() => {
                    this.pictureForm = Object.assign(this.pictureForm,formData)
                    console.log("表单数据为：",this.pictureForm)
                })
            });
        },
        getBase64(file) {
            return new Promise(function(resolve, reject) {
                let reader = new FileReader();
                let imgResult = "";
                reader.readAsDataURL(file);
                reader.onload = function() {
                imgResult = reader.result;
                };
                reader.onerror = function(error) {
                reject(error);
                };
                reader.onloadend = function() {
                resolve(imgResult);
                };
            });
        },
        clickPictureUrl(row) {
            // window.open(row.pictureUrl)
            const img = new window.Image();
            img.src = row.pictureUrl;

            const newWin = window.open('');
            newWin.document.body.style.background = '#000';
            newWin.document.body.style.textAlign = 'center';
            newWin.document.body.appendChild(img);
            newWin.document.title = '图片预览';
            newWin.document.close();
        }
    }
  }
</script>

<style>
  .bg-purple{
      text-align: left;
  }
  .bg-purple-light{
      text-align: right;
  }
  .el-date-editor.el-input{
    width: 100%;
  }
</style>